import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
	data() {
		return {
			j: 1,
			id:'',
			commentId:'',
			title:'评论',
			index: 0,
			ratingsList: ''
		};
	},
	onLoad(e) {
		this.id = e.id;
	},
	onShow() {
		let that = this;
		console.log('that.id'+that.id)
		uni.request({
			url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/comment/by/scenicId',
			method: 'POST',
			data: {
				scenicSpotId: that.id,
				pageNum: that.j,
			},
			success: res => {
				console.log(res.data);
				this.ratingsList = res.data.data.commentScenicResponseList;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onReachBottom() {
		this.j++;
		let that = this;
		setTimeout(() => {
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/comment/by/scenicId',
				method: 'POST',
				data: {
					scenicSpotId: that.id,
					pageNum: that.j,
				},
				success: res => {
					let ratingsPush = [];
					ratingsPush = res.data.data.commentScenicResponseList;
					for (let i = 0; i < ratingsPush.length; i++) {
					that.ratingsList.push(ratingsPush[i]);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}, 500);
	},
	methods: {
		tapReply(e, idx) {
			if (this.index != idx) {
				this.index = idx;
				// console.log(e)
			} else {
				this.index = null;
			}
		},
		// ismy(index){
		// 	return [index].userId==this.userId
		// },
		deleteComment(commentId){
			
			let that = this;
			console.log(that.userId)
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/remove/comment',
				method: 'DELETE',
				data: {
					userId: that.userId,
					commentId: commentId
				},
				success: res => {
					console.log(res)
					onshow();
					uni.showToast({
						title:"删除成功"
					})
				},
				fail: () => {},
				complete: () => {
					uni.hideToast()
				}
			});
		}
	}
};
